.contacts {
  background-color: white;
  background-image: linear-gradient(#ffffff 176px, #fff5f6 176px);
  background-repeat: no-repeat;

  &__cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
    //  min-width: 320px;
    margin-left: auto;
    margin-right: auto;
    padding: 86px 50px 0px 50px;
    //width: 220px;
    //height: 100%;
  }

  &__item {
    max-width: 220px;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    background-color: #ffffff;
    margin-bottom: 20px;
    &:hover {
      transform: scale(1.15);
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
  }
  &__card {
    //height: 100%;
    padding: 43px 17px;
  }

  &__content {
    margin: 0;
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
  }
  &__title {
    width: 80px;
    background-color: #f0d1a5;
    display: inline-block;
    text-transform: uppercase;
    border-radius: 4px;
    margin: 0 0 25px;
    padding: 0;
    text-align: center;
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.04em;
    color: #ffffff;
    &--green {
      width: 160px;

      display: inline-block;
      text-transform: uppercase;
      border-radius: 4px;
      margin: 0 0 25px;
      padding: 0;
      text-align: center;
      font-family: DM Sans;
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.04em;
      color: #ffffff;

      background-color: #c2e297;
    }
  }
  &__label {
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.04em;
  }

  &__text {
    margin: 10px 0 50px;
    color: #907e82;
    text-align: left;
  }

  &__text::after {
    margin-top: 23px;
    content: '';
    display: inline-block;
    width: 100%;
    border: 1px solid #e1e1e1;
  }

  &__time {
    margin: 0;
    color: #ffa5ba;
  }
  &__mail {
    display: inline-block;
    margin: 0;
    color: #907e82;
    text-decoration: none;
  }

  &__list {
    //width: 220px;
    //height: 427px;
    margin-right: auto;
    margin-left: auto;
  }
}

.schedule {
  &__item:first-child {
    margin-bottom: 10px;
  }
  // &__item:last-child {
  //   //margin-bottom: 50px;
  // }

  &__time {
    margin: 0;
    color: #ffa5ba;
  }
}

.schedule::after {
  margin-top: 23px;
  margin-bottom: 23px;
  content: '';
  display: inline-block;
  width: 100%;
  border: 1px solid #e1e1e1;
}

