.button {
  margin-left: auto;
  margin-right: auto;

  margin-top: 54px;
  margin-bottom: 113px;

  align-items: center;
  justify-content: center;
  @media screen and (min-width: 768px) {
    display: flex;
  }

  &-contacts__but1 {
    border-radius: 22px;
    width: 161.45px;
    height: 44px;
    padding-right: 20px;
    background: #d41342;
    border: none;
    margin-bottom: 10px;
    position: relative;
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: $button-red-bc-hover;
    }
  }

  &-contacts__but2 {
    border-radius: 22px;
    width: 161px;
    height: 44px;
    padding-right: 20px;
    background: #ffffff;
    border: none;
    position: relative;
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: $button-bc-hover;
    }
  }

  .button__img-pink {
    position: absolute;
    top: 19px;
    right: 36px;
  }

  .button__img-white {
    position: absolute;
    top: 18px;
    right: 26px;

    @media screen and (max-width: 768px) {
      right: 19px;
    }
  }

  .button-text__white {
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;

    font-size: 16px;
    line-height: 21px;

    align-items: center;
    text-align: center;

    color: $white;
  }

  .button-text__pink {
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;

    font-size: 16px;
    line-height: 21px;

    align-items: center;
    text-align: center;

    color: $main-text-color;
  }
}

@media screen and (min-width: 768px) {
  .contacts__list {
    display: flex;
    margin-left: -20px;
  }

  .contacts__item {
    margin-left: 20px;
    flex-basis: calc(100% / 3-20px);
  }

  .contacts__cards {
    padding: 119px 34px 0px 34px;
    width: 768px;
  }

  .contacts__text {
    font-size: 14px;
  }

  .contacts__text::after {
    margin-top: 28px;
  }

  .contacts__card {
    padding: 50px 30px 50px 30px;
  }

  .contacts__title {
    margin-bottom: 30px;
    font-size: 16px;
    width: 73px;
    line-height: 28px;

    &--green {
      margin-bottom: 30px;
      width: 138px;
      font-size: 16px;
      line-height: 28px;
    }
  }

  .contacts__text {
    margin-top: 14px;
    margin-bottom: 60px;
  }

  .schedule__item:first-child {
    margin-bottom: 16px;
  }

  //.schedule__item:last-child {
  // margin-bottom: 60px;
  //}

  .schedule::after {
    margin: 28px 0;
  }

  .contacts__label {
    font-size: 14px;
  }

  .schedule__time {
    font-size: 14px;
    line-height: 26px;
  }

  .contacts__mail {
    font-size: 14px;
    line-height: 26px;
  }

  .button__div {
    display: flex;
    justify-content: center;
    align-items: baseline;
  }

  .button-contacts__but1 {
    margin-bottom: 0;
    margin-right: 15px;
    width: 192.45px;
    height: 44px;
  }

  .button-contacts__but2 {
    width: 175px;
    height: 44px;
  }
}

@media screen and (min-width: 1280px) {
  .contacts {
    &__title {
      margin-bottom: 32px;

      &--green {
        margin-bottom: 32px;
      }
    }

    &__list {
      margin-left: -30px;
    }

    &__item {
      min-width: 330px;
      margin-left: 30px;
      flex-basis: calc(100% / 3 - 30px);
    }

    &__cards {
      padding: 119px 115px 0px 115px;
      min-width: 1280px;
    }

    &__label {
      font-size: 16px;
    }
    &__text {
      margin-top: 10px;
      margin-bottom: 30px;
    }
    .contacts__text::after {
      margin-top: 30px;
    }

    &__label {
      font-size: 16px;
    }

    &__mail {
      font-size: 16px;
      line-height: 30px;
    }
  }

  .schedule__time {
    font-size: 16px;
    line-height: 30px;
  }

  //.schedule__item:last-child {
  // margin-bottom: 64px;
  //}
  .schedule::after {
    margin: 30px 0;
  }
  .schedule__item:first-child {
    margin-bottom: 15px;
  }

  .contacts__card {
    padding: 52px 43px 62px 43px;
  }
}
