.products {
  padding-top: 120px;
  padding-bottom: 98px;
  //background-color: wheat;

  &__description {
    font-family: $second-font;
    color: $second-text-color;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 18px;
    text-transform: uppercase;
    text-align: center;
    line-height: 1.14;
  }

  &__title {
    font-family: $second-font;
    color: $main-text-color;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 34px;
    text-transform: uppercase;
    text-align: center;
    line-height: 1.15;
    letter-spacing: 0.04em;
  }
}

.products-list {
  margin-right: auto;
  margin-left: auto;

  //padding-bottom: 30px;

  &__title {
    font-family: $second-font;
    font-weight: 400;
    font-size: 20px;
    text-transform: uppercase;
    text-align: center;
    line-height: 1.15;
    letter-spacing: 0.04em;
    color: $white;
    position: relative;
    //padding-bottom: 30px;

    &::after {
      content: '';
      position: absolute;
      width: 26px;
      height: 6px;
      top: 100%;
      left: 50%;
      //fill
      transform: translate(-50%, -50%);
      background-image: url('../images/products-dots.svg');
    }
  }

  &__text {
    text-align: center;
    color: $white;
    letter-spacing: 0.04em;
    //margin-bottom: 30px;
  }

  &__button-icon {
    width: 15px;
    height: 15px;
  }
  &__button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    //background-color: $white;
    color: $button-red-bc-hover;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    outline: none;
    //color
    //hover
    //focus

    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      background-color: $button-red-bc-hover;
      color: $white;
    }
  }
}

.products-list__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 24px;
  position: relative;
  //margin-top: 135px;
  //padding: 145px 42px 40px;
  &:hover {
    transform: scale(1.15);
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  &.item-icecream {
    background-color: $bc-hero;
    //margin-bottom: 16px;
    &::before {
      background-image: url('../images/icecream249.png');

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url('../images/icecream249x2.png');
      }
    }
  }

  &.item-icecoffee {
    background-color: #f0d1a5;
    //margin-bottom: 16px;

    &::before {
      background-image: url('../images/icecoffee249.png');

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url('../images/icecoffee249x2.png');
      }
    }
  }

  &.item-milkshakes {
    background-color: #c2e297;

    &::before {
      background-image: url('../images/milkshakes249.png');

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url('../images/milkshakes249x2.png');
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    background-repeat: no-repeat;
    background-size: cover;
    top: 0%;
    transform: translate(0, -50%);
    width: 250px;
    height: 250px;
  }
}

@media screen and (min-width: 320px) {
  .products-container {
    width: 320px;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
  }

  .products-list {
    &__title {
      padding-bottom: 30px;
    }
    &__text {
      margin-top: 30px;
      font-size: 14px;
      line-height: 1.52;
    }
    &__button {
      margin-top: 30px;
    }
  }

  .products-list__item {
    padding: 186px 42px 40px;
    margin-top: 130px;

    &::before {
      width: 250px;
      height: 250px;
    }
  }
}

@media screen and (min-width: 768px) {
  .products-container {
    width: 768px;
    padding: 0 34px;
  }

  .products {
    padding-top: 122px;
    padding-bottom: 112px;
    &__description {
      font-size: 26px;
      line-height: 1.15;
    }
    &__title {
      font-size: 48px;
      line-height: 1.14;
      margin-bottom: 39px;
    }
  }

  .products-list {
    display: flex;
    margin-left: -20px;

    &__button {
      margin-top: 40px;
    }
  }

  .products-list__item {
    margin-top: 150px;
    margin-left: 20px;

    padding: 197px 10px 40px 10px;

    flex-basis: calc(100% / 3-20px);

    &::before {
      width: 220px;
      height: 233px;
    }

    &.item-icecream {
      //margin-bottom: 0px;
      &::before {
        background-image: url('../images/icecream220.png');

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url('../images/icecream220x2.png');
        }
      }
    }

    &.item-icecoffee {
      //margin-bottom: 0px;

      &::before {
        background-image: url('../images/icecoffee220.png');

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url('../images/icecoffee220x2.png');
        }
      }
    }

    &.item-milkshakes {
      //margin-bottom: 0px;
      &::before {
        background-image: url('../images/milkshakes220.png');

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url('../images/milkshakes220x2.png');
        }
      }
    }
  }
}

@media screen and (min-width: 1280px) {
  .products-container {
    width: 1280px;
    padding: 0 115px;
  }

  .products {
    padding: 127px 0 119px;

    &__description {
      font-size: 30px;
    }

    &__title {
      font-size: 58px;
      margin-bottom: 49px;
    }
  }

  .products-list {
    display: flex;
    margin-left: -30px;

    &__title {
      margin-top: 38px;
      padding-bottom: 32px;
      font-size: 30px;
    }

    &__text {
      font-size: 16px;
      margin-top: 32px;
    }

    &__button {
      margin-top: 62px;
    }
  }

  .products-list__item {
    margin-top: 210px;
    margin-left: 30px;

    flex-basis: calc(100% / 3-30px);

    padding: 140px 44px 50px 44px;

    &::before {
      width: 335px;
      height: 335px;
    }

    &.item-icecream {
      //margin-bottom: 0px;
      &::before {
        background-image: url('../images/icecream335.png');

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url('../images/icecream335x2.png');
        }
      }
    }

    &.item-icecoffee {
      //margin-bottom: 0px;

      &::before {
        background-image: url('../images/icecoffee337.png');

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url('../images/icecoffee337x2.png');
        }
      }
    }

    &.item-milkshakes {
      //margin-bottom: 0px;
      &::before {
        background-image: url('../images/milkshakes335.png');

        @media screen and (min-device-pixel-ratio: 2),
          screen and (min-resolution: 192dpi),
          screen and (min-resolution: 2dppx) {
          background-image: url('../images/milkshakes335x2.png');
        }
      }
    }
  }
}
